<route>
{
  "meta": {
     "title": "Recordings"
  }
}
</route>
<template>

  <div>

    <v-row class="mb-3">

      <v-col>
        <data-loader :handler="loadFilter">
          <template v-slot:loading>
            <data-card>
              <v-skeleton-loader type="card-heading,list-item-two-line"></v-skeleton-loader>
            </data-card>
          </template>
          <template v-slot:default="{ data: { filters } = {}  }">
            <data-card title="Date Range">
              <v-row class="align-center px-4 py-2 mb-0">
                <data-bit-picker class-actual="col-auto py-1" v-model="filter.dateMin" :schema="filters.date" def-prop="min">
                  <template v-slot:default="{ value }"><data-bit title="Start Date">{{value}}</data-bit></template>
                </data-bit-picker>
                <data-bit class="col-auto py-0 pt-4" title="to"></data-bit>
                <data-bit-picker class-actual="col-auto py-1" v-model="filter.dateMax" :schema="filters.date" def-prop="max">
                  <template v-slot:default="{ value }"><data-bit title="End Date">{{value}}</data-bit></template>
                </data-bit-picker>
              </v-row>
            </data-card>
          </template>
        </data-loader>
      </v-col>

      <v-col>
        <data-loader :handler="loadFilter">
          <template v-slot:loading>
            <data-card>
              <v-skeleton-loader type="card-heading,list-item-two-line"></v-skeleton-loader>
            </data-card>
          </template>
          <template v-slot:default="{ data: { } = {} }">
            <data-card title="Time Range">
              <v-row class="align-center px-4 py-2 mb-0">
                <data-bit-picker class-actual="col-auto py-1" v-model="filter.timeMin" def="00:00" type="time">
                  <template v-slot:default="{ value }"><data-bit title="Start Time">{{value}}</data-bit></template>
                </data-bit-picker>
                <data-bit class="col-auto py-0 pt-4" title="to"></data-bit>
                <data-bit-picker class-actual="col-auto py-1" v-model="filter.timeMax" def="00:00" type="time">
                  <template v-slot:default="{ value }"><data-bit title="End Time">{{value}}</data-bit></template>
                </data-bit-picker>
              </v-row>
            </data-card>
          </template>
        </data-loader>
      </v-col>

      <v-col>
        <data-loader :handler="loadSummary">
          <template v-slot:loading>
            <data-card>
              <v-skeleton-loader type="card-heading,list-item-two-line"></v-skeleton-loader>
            </data-card>
          </template>
          <template v-slot:default="{  }">
            <data-card title="MTD Queue Numbers">
              <v-row class="align-center px-4 py-2 mb-0">
                <data-bit class="col-auto py-1" title="Outstanding">20</data-bit>
                <data-bit class="col-auto py-1" title="Review Completed">350</data-bit>
              </v-row>
            </data-card>
          </template>
        </data-loader>
      </v-col>

    </v-row>

    <data-loader :handler="loadTable">
      <template v-slot:loading>
        <v-card class="py-2">
          <v-skeleton-loader type="table-thead, table-tbody, table-tfoot"></v-skeleton-loader>
        </v-card>
      </template>

      <template v-slot:default="{ data: { reviews } = {} }">
        <v-card>
          <v-card-text>
            <AppDataTable :headers="headers" :items="reviews" >
              <template #item.email="{ value }">
                <a :href="`mailto:${value}`">{{ value }}</a>
              </template>
              <template #item.phone="{ value }">
                <a :href="`tel:${value}`">{{ value }}</a>
              </template>
              <template #item.date="{ value }">
                <a href>{{ value }}</a>
              </template>
              <template #item.duration="{ value }">
                <span>{{ value }}</span>
                <v-btn icon x-small color="primary" class="ml-1">
                  <v-icon>mdi-download</v-icon>
                </v-btn>
                <v-btn icon x-small color="primary" class="ml-1">
                  <v-icon>mdi-play-circle</v-icon>
                </v-btn>
              </template>
            </AppDataTable>

          </v-card-text>
        </v-card>
      </template>

    </data-loader>

  </div>

</template>

<script>
import AppDataTable from '@/components/AppDataTable'
import DataBit from '@/components/DataBit'
import DataBitPicker from '@/components/DataBitPicker'
import DataCard from '@/components/DataCard'

export default {
  name: 'Queue',
  components: {
    AppDataTable,
    DataBit,
    DataBitPicker,
    DataCard
  },

  data: () => ({
    filter: {
      dateMin: null,
      dateMax: null,
      timeMin: null,
      timeMax: null
    },
    headers: [
      { text: 'Date', value: 'date', align: 'start', sortable: false },
      { text: 'Priority', value: 'priority' },
      { text: 'Defect', value: 'defect' },
      { text: 'Phone', value: 'phone' },
      { text: 'Email', value: 'email' },
      { text: 'Duration', value: 'duration' },
      { text: 'Status', value: 'status' }
    ]
  }),

  methods: {
    loadFilter () {
      return this.$store.dispatch('api/request', 'apiGetRecordingsFilters')
    },

    loadSummary () {
      return this.$store.dispatch('api/request', 'apiGetRecordingsSummary')
    },

    loadTable () {
      return this.$store.dispatch('api/request', 'apiGetRecordings')
    }
  }
}
</script>
